.dropdown {
  position: relative;

  &:hover {
    .dropdown__icon::after {
      transform: translateY(-50%) rotate(180deg);
    }

    &::before {
      content: "";
      position: absolute;
      bottom: -10px;
      height: 20px;
      width: 100%;
      background: transparent;
    }

    > .dropdown__menu {
      display: block;
      opacity: 1;
      visibility: visible;
      @media (min-width: $width-tablet-h) {
        position: absolute;
        top: calc(100% + 10px);
        min-width: 100%;
      }
    }
  }
}
.dropdown__icon {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 13px;
    height: 12px;
    background: url("data:image/svg+xml,%3Csvg width='11' height='9' viewBox='0 0 11 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.46871 8.43439C6.04061 9.11226 5.05207 9.11225 4.62397 8.43439L0.934642 2.59259C0.475889 1.86619 0.997874 0.919172 1.85701 0.919172L9.23567 0.919173C10.0948 0.919173 10.6168 1.86619 10.158 2.59259L6.46871 8.43439Z' fill='%23331A2F' fill-opacity='0.9'/%3E%3C/svg%3E%0A")
      no-repeat center;
    transition: transform 0.3s ease 0s;
  }
}

.dropdown__menu {
  display: none;
  opacity: 0;
  visibility: hidden;

  @media (min-width: $width-tablet-h) {
    box-shadow: $form-box-shadow;
    border-radius: $form-border-radius;
  }
}
